import { Component, Event, EventEmitter, h, Host, Listen, Prop, State } from '@stencil/core';
import { MediaQueryDetector } from '../../../../assets/shared/scripts/media-query-detector';

@Component({
  tag: 'wink-flyout',
  styleUrls: {
    telenet: 'styles/brands/flyout.telenet.scss',
    base: 'styles/brands/flyout.base.scss',
  },
  shadow: true,
})
export class Flyout {
  @Prop() flyoutId: number | undefined = undefined;

  @State() activeId: number | undefined = undefined;
  @State() backLabel: string | undefined = undefined;

  @Event() flyoutBackLinkClicked: EventEmitter | undefined;

  @Listen('mainNavigationItemClicked', { target: 'document' })
  handleMainNavigationItemClicked(event: CustomEvent<{ index: number; name: string }>) {
    this.activeId = event.detail.index;
    this.backLabel = event.detail.name;
  }

  private handleFlyoutBackLinkClicked() {
    this.activeId = undefined;
    this.flyoutBackLinkClicked?.emit();
  }

  private handleFlyoutMouseLeave() {
    if (MediaQueryDetector.isUntilMdHeader) {
      return;
    }
    this.activeId = undefined;
  }

  render() {
    return (
      <Host
        id={this.flyoutId}
        aria-label={this.flyoutId === this.activeId ? this.backLabel : ''}
        {...{ open: this.flyoutId === this.activeId }}
        onMouseLeave={this.handleFlyoutMouseLeave.bind(this)}>
        <div class="container">
          <div class="content">
            <wink-link
              onClick={this.handleFlyoutBackLinkClicked.bind(this)}
              appearance="menu-back"
              icon="arrow-left"
              size="caption-bold"
              icon-position="left"
              icon-appearance="default"
              is-disabled="false"
              href="#">
              <wink-icon
                slot="link-icon"
                appearance="default"
                icon="arrow-left"
                size="md"
                exportparts="icon, icon-arrow-left"
              />
              {this.backLabel}
            </wink-link>

            {Array.from({ length: 4 }).map((_, index) => (
              <slot name={`col-${index + 1}`} />
            ))}
          </div>
        </div>
      </Host>
    );
  }
}
